<template>
    <section class="resume-section" id="projects">
    <div class="resume-section-content">
      <h2 class="mb-5">Projects</h2>
      <p class="lead mb-0">
        Below you can see a few examples of projects and applications I've worked on.
      </p>


        <!-- Table -->
    <table>
        <tr v-for="(project, i) in projects" :key=i>
          <td>
            <a :href="project.link" target="_blank"><img style="width: 400px; height:225px;" :src="images[i]" alt="..."></a>
            <p class="lead mt-3 mb-1">{{ project.title }}</p>
            <p>{{ project.description }}</p>
            <p><a :href="project.link" target="_blank">GitHub</a></p>
          </td>
        </tr>
    </table>
    </div>
  </section>
</template>

<script>
export default {
    name: "projects",

    data() {
      return {
        projects: [],
        images: [
          "img/Pizza.PNG",
          "img/Hospital.PNG",
          "img/Rentals.PNG",
        ],
      }
    },

    methods: {
        listData: function () {
          let $this = this;
          $.ajax({
            type: "GET",
            url: `${baseURL}/projects/list`,
            dataType: "json",
            success: function (data, status) {
              if (status === 'success') {
                $this.projects = data;
              }
            }
          })
        },
    },

    mounted() {
        this.listData();
      }
}
</script>